import { Component, OnInit } from '@angular/core';
import { ProductServiceService } from '../product-service.service';

@Component({
  selector: 'app-categories',
  templateUrl: './categories.component.html',
  styleUrls: ['./categories.component.css']
})
export class CategoriesComponent implements OnInit {

  ngOnInit() {this.productServiceService.setCategories(this.getCategories());}

  //constructor(private productServiceService: ProductServiceService){}
  constructor(private productServiceService: ProductServiceService){}

  categories: string[] = ["owoce", "slodkie","niezdrowe"];
  checked: string[] = [];
  filterName: string = "";


  public catEmitted(categories: string[]): void {
    this.categories = categories;
  }

  clicked(cat: string): void{
    if (this.checked.indexOf(cat)==-1) {
      this.checked.push(cat);
    }else{
      let index= this.checked.indexOf(cat);
      this.checked.splice(index,1);
    }
    this.checked = this.getCopy(this.checked);
    this.productServiceService.setCategories(this.getCategories());
  }
  getCopy(cat: string[]): string[]{
    let newcat: string[] = [];
    for(let c of cat){
      newcat.push(c);
    }
    return newcat;
  }

  isActive(cat: string): string{
    if(this.checked.indexOf(cat)!=-1){
      return "checked";
    }else{
      return "unchecked";
    }
  }

  getCategories(){
    if(this.checked.length==0){
      return this.getCopy(this.categories);
    }
    else{
      return this.getCopy(this.checked);
    }
  }
}
