import { Component, OnInit } from '@angular/core';
import {ProduktComponent} from '../produkt/produkt.component';
import {TrolleyServiceService} from '../trolley-service.service';
import {and} from '@angular/router/src/utils/collection';
import {ProductServiceService} from '../product-service.service';
import {AuthService} from '../auth-service.service';

@Component({
  selector: 'app-trolley',
  templateUrl: './trolley.component.html',
  styleUrls: ['./trolley.component.css']
})
export class TrolleyComponent implements OnInit {

  productsInCart: ProduktComponent[] = [];
  totalCost:number = 0;
  howManyProducts:number=0;

  constructor(private authService: AuthService, private trolleyServiceService: TrolleyServiceService, private productServiceService: ProductServiceService) {}

  ngOnInit() {
   // this.getProducts();
    this.trolleyServiceService.getProductsFromTheCart().subscribe(products => {this.productsInCart = products;});
    this.calcutaleCost();
    this.calculateHowManyProducts();
  }

  options = [5, 10, 20];
  productsPageIndex = 1;
  productsOnPage = 5;

  public amountOfItemsOnThePage(s: string){
    if(s=="+"){
      if(this.productsOnPage<this.productsInCart.length) {
        this.productsOnPage = this.productsOnPage * 2;
        if(this.productsOnPage>this.productsInCart.length){
          this.productsOnPage=this.productsInCart.length;
        }
      }
      if(((this.productsInCart.length/this.productsOnPage)<this.productsPageIndex)&&(this.productsPageIndex>1)){
        this.productsPageIndex--;
      }

    }
    else if (s=="-"){
      if(this.productsOnPage>=10) {
        this.productsOnPage = this.productsOnPage / 2;
      }
      else{
        this.productsOnPage=5;
      }
    }
  }


  public setPageTo(x){
    this.productsPageIndex = x;
    this.getProcuctsForPage();
  }

  public getPagesCount(){
    let pagesCount = this.productsInCart.length / this.productsOnPage;
    if(this.productsInCart.length % this.productsOnPage > 0){
      pagesCount++;
    }
    let x=0;
    for (let i = 1; i <= pagesCount; i++) {
      x++;
    }
    return x;
  }

  public getPagesArray(){
    let res = [];
    let pagesCount = this.getPagesCount();
    for (let i = 1; i <= pagesCount; i++) {
      res.push(i);
    }
    return res;
  }


  public getProcuctsForPage(){
    return this.productsInCart.slice((this.productsPageIndex-1)*this.productsOnPage,(this.productsPageIndex)*this.productsOnPage);
  }



  getProducts(){
    //this.productsInCart = this.trolleyServiceService.getProductsFromTheCart();
      this.trolleyServiceService.getProductsFromTheCart().subscribe(products => {this.productsInCart = products;});
      this.calcutaleCost();
      this.calculateHowManyProducts();
  }

  addToCart(product: ProduktComponent){
    if(this.productServiceService.getProduct(product).products_count>0){
      this.productServiceService.deleteOneProduct(product);
      this.trolleyServiceService.addOneProductToTheCart(product);
      this.calcutaleCost();
      this.calculateHowManyProducts();
    }
  }

  addProduct(product : ProduktComponent){
    for(let p of this.productsInCart){
      if(p.name == product.name && p.description == product.description){
        p.products_count += product.products_count;
        return;
      }
    }
    this.productsInCart.push(product);
  }

  calcutaleCost(){
    let cost : number = 0;
    for(let p of this.productsInCart) {
      cost += p.products_count * p.price;
    }

    this.totalCost=cost;
  }

  calculateHowManyProducts(){
    let howMany : number = 0;
    for(let p of this.productsInCart) {
      howMany += p.products_count;
    }
    this.howManyProducts = howMany;
  }


  public removeFromCart(item){
    let itemFromCartService = this.trolleyServiceService.getProductFromCart(item);
    if(itemFromCartService!=null && itemFromCartService.products_count>0){
      let cartItem = Object.assign({}, item);
      this.trolleyServiceService.deleteOneProductFromTheCart(cartItem);
      this.productServiceService.addOneProduct(cartItem);
      this.getProducts();
    }
  }



}
