import {OrderProductComponent} from '../order-product/order-product.component';

export class Order{

  id: string;
  name: string;
  address: string;
  email: string;
  tel: string;
  date: string;
  state: string;
  products: OrderProductComponent[] = [];

  constructor(id: string, name: string, address: string, email: string, tel: string, date: string, state: string, products: OrderProductComponent[]) {
    this.id = id;
    this.name = name;
    this.address = address;
    this.email = email;
    this.tel = tel;
    this.date = date;
    this.state = state;
    this.products = products;

  }

}
