import { FormsModule } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import {ProduktComponent} from '../produkt/produkt.component';
import {Order} from '../models/order';
import {TrolleyServiceService} from '../trolley-service.service';
import {OrdersComponent} from '../orders/orders.component';
import {AngularFireDatabase} from '@angular/fire/database';
import {OrderProductComponent} from '../order-product/order-product.component';
import {AuthService} from '../auth-service.service';
import {AppComponent} from '../app.component';
import {HttpClient} from '@angular/common/http';

@Component({
  selector: 'app-buyform',
  templateUrl: './buyform.component.html',
  styleUrls: ['./buyform.component.css']
})
export class BuyformComponent implements OnInit {

  name: string;
  address: string;
  email: string;
  tel: string;
  errorMsg: string;

  constructor(private http: HttpClient, private appcomponent: AppComponent, private authService: AuthService, private trolleyServiceService: TrolleyServiceService, private db: AngularFireDatabase) { }

  ngOnInit() {
  }

  onSubmit() {
    this.trolleyServiceService.getProductsFromTheCart().subscribe(products => {

      if (products.length > 0) {
          const orderProducts: OrderProductComponent[] = [];
          products.map(p => {
            return new OrderProductComponent(false, p.id, p.name, p.products_count, p.price, p.description, p.link, p.category)
          }).forEach(item => orderProducts.push(item));
        let id;
        if (this.appcomponent.databaseMode=="fire") {
          id = this.db.createPushId();
        } else {
          id = this.appcomponent.getUniqueId();
        }
        const neworder: Order = new Order(id, this.name, this.address, this.email, this.tel, "1", 'waiting', orderProducts);
        /*  alert("id: "+id);*/
        // this.db.list("/orders").push(neworder);
        if (this.appcomponent.databaseMode=="fire") {
          this.db.object('/orders/' + id)
            .update(neworder);
          this.clearForm();
          this.trolleyServiceService.clear();
        } else {
          this.http.put(this.appcomponent.url + "/orders/" + id, {
            id: neworder.id, name: neworder.name, address: neworder.address,
            email: neworder.email, tel: neworder.tel, date: neworder.date, state: neworder.state, products: orderProducts
          }).subscribe(
            res => {
              console.log(res);
            },
            err => {
              console.log("Error occured");
            }
          );
          this.clearForm();
          this.trolleyServiceService.clear();
        }
      } else {
          this.errorMsg = "The trolley is empty";
      }

    });
  }



  clearForm(){
    this.name="";
    this.address="";
    this.email="";
    this.tel="";
    this.errorMsg="";

  }

}
