import { Component, OnInit } from '@angular/core';
import {ProduktComponent} from '../produkt/produkt.component';
import { ProductServiceService } from '../product-service.service';
import {AngularFireDatabase} from '@angular/fire/database';
import {AppComponent} from '../app.component';

@Component({
  selector: 'app-new-product',
  templateUrl: './new-product.component.html',
  styleUrls: ['./new-product.component.css'],
  template: `
    <div>
    name:          <input #name (keyup)="0">
    </div>
    <div>
    description:   <input #description (keyup)="0">
    </div>
    <div>
    product count: <input #productCount (keyup)="0">
    </div>
    <div>
    price:         <input #price (keyup)="0">
    </div>
    <div>
    link:          <input #link (keyup)="0">
    </div>
    <div>
    <div>
    category:   <input #category (keyup)="0">
    </div>
    <button (click)="addProduct(name.value,description.value,productCount.value,price.value,link.value,category.value)">Add product</button>
    </div>
  `

})
export class NewProductComponent implements OnInit {

  addProduct(name: string, description: string, productCount: string, price: string, link: string, category: string){
    let id;
    if (this.appcomponent.databaseMode=="fire") {
      id = this.db.createPushId();
    } else {
      id = this.appcomponent.getUniqueId();
    }
    this.productServiceService.addProduct(new ProduktComponent(id, name, Number(productCount), Number(price), description, link, category));
  }

  constructor(private appcomponent: AppComponent, private productServiceService: ProductServiceService, private db: AngularFireDatabase) { }

  ngOnInit() {
  }

}
