import { Component, OnInit } from '@angular/core';
import {AngularFireDatabase} from '@angular/fire/database';
import {Order} from '../models/order';
import {ProduktComponent} from '../produkt/produkt.component';
import {OrderProductComponent} from '../order-product/order-product.component';
import {assertLessThan} from '@angular/core/src/render3/assert';
import {DatePipe} from '@angular/common';
import {AppComponent} from '../app.component';
import {HttpClient} from '@angular/common/http';

@Component({
  selector: 'app-orders',
  templateUrl: './orders.component.html',
  styleUrls: ['./orders.component.css']
})
export class OrdersComponent implements OnInit {

  orders: Order[];
  idx: string;
  products: OrderProductComponent[];
  flag = false;
  options="1";

  constructor(private http: HttpClient, private appcomponent: AppComponent, private db: AngularFireDatabase) {this.flag = false; }

  ngOnInit() {
    if (this.appcomponent.databaseMode=="fire") {
      this.db.list('/orders').valueChanges().subscribe(orders => {
        this.orders = <Order[]> orders;
      });
    } else {
      this.http.get(this.appcomponent.url + "/orders").subscribe(
        orders => {
          this.orders = <Order[]> orders;
        });
    }
  }

  checkState(order){
    if (this.options == "1" && order.state === 'waiting') { return true; };
    if (this.options == "2" && ( order.state === 'inprogres' || order.state === 'done' )) { return true; };
    if (this.options == "3" && order.state === 'sent') { return true; };
    return false;
  }

  makeAllCompleted() {
    const order: Order = this.orders.find(x => x.id === this.idx);
    if (order.state !== "sent") {
      for (const x of this.products) {
        x.isCompleted = true;
      }
      order.state = 'done';
    }
  }

  goToDetails(products: OrderProductComponent[], idx: string) {
    this.flag = true;
    this.products = products;
    this.idx = idx;
  }

  getRadioClass(nr: string) {
    if (nr === this.options) {
      return "btn btn-secondary active";
    }
    return "btn btn-secondary";
  }

  getStatus(): string {
    if (this.products.find(x => x.isCompleted === true) !== undefined &&  this.products.find(x => x.isCompleted === false) !== undefined) {
      return 'inprogres';
    } else if (this.products.find(x => x.isCompleted === false) === undefined) {
      return 'done';
    } else if (this.products.find(x => x.isCompleted === true) === undefined) {
      return 'waiting';
    }
    return 'BADRESULT';
  }

  updateOrderInDB(order: Order): void{
    order.products.find(x => x.isCompleted===true);
    let o: Order = new Order( order.id, order.name, order.address, order.email, order.tel, order.date, order.state, order.products);
    if (this.appcomponent.databaseMode=="fire") {
      this.db.object('/orders/' + order.id).update(o);
    } else {
      this.http.put(this.appcomponent.url + "/orders/" + o.id, {
        id: o.id, name: o.name, address: o.address,
        email: o.email, tel: o.tel, date: o.date, state: o.state, products: o.products
      }).subscribe(
        res => {
          console.log(res);
        },
        err => {
          console.log("Error occured");
        }
      );
    }
  }

  sendOrder(){
    const order: Order = this.orders.find(x => x.id === this.idx);
    order.state = 'sent';
    //order.date = Date.now();
    var datePipe = new DatePipe('en-US');
    order.date = datePipe.transform(Date.now(), 'dd/MM/yyyy hh:mm');
    this.updateOrderInDB(order);
  }

  markAsDone(product: OrderProductComponent) {
    product.isCompleted = true;
    const order: Order = this.orders.find(x => x.id === this.idx);
    order.products = this.products;
    order.state = this.getStatus();
    this.updateOrderInDB(order);
  }

  calculateCost(products: OrderProductComponent[]) {
    let cost = 0;
    for (const p of products) {
      cost += (p.products_count * p.price);
    }
    return cost;
  }
}
