import { Component } from '@angular/core';

@Component({
  selector: 'app-produkt',
  templateUrl: './produkt.component.html',
  styleUrls: ['./produkt.component.css'],

})
export class ProduktComponent{

  id: string;
  name: string;
  products_count: number;
  price: number;
  description: string;
  link: string;
  category: string;

  constructor(id:string, name:string, products_count:number, price: number, description:string, link: string, category: string) {
    this.id=id;
    this.name=name;
    this.products_count=products_count;
    this.price=price;
    this.description=description;
    this.link=link;
    this.category=category;
  }


}
