  getProductsForCategories(){
    this.categories=this.productServiceService.categories;
    return this.produkts.filter(element => this.categories.find(x => x == element.category));
  }

  public getPagesCount(){
    let productsAfterFiltering = this.getProductsForCategories();
    let pagesCount = productsAfterFiltering.length / this.productsOnPage;
    if(productsAfterFiltering.length % this.productsOnPage > 0){
      pagesCount++;
    }
    let x=0;
    for (let i = 1; i <= pagesCount; i++) {
      x++;
    }
    return x;
  }

  public getPagesArray(){
    let res = [];
    let pagesCount = this.getPagesCount();
    for (let i = 1; i <= pagesCount; i++) {
      res.push(i);
    }
    return res;
  }


  public getProcuctsForPage(){
    let productsAfterFiltering = this.getProductsForCategories();
    return productsAfterFiltering.slice((this.productsPageIndex-1)*this.productsOnPage,(this.productsPageIndex)*this.productsOnPage);
  }
